import React,{useEffect, useRef, useState} from "react"
import './index.css';
import $ from 'jquery' ;
import {withRouter} from "react-router-dom";
function Login_register(props){

  const[admin,setadmin]=useState("");
  const[password,setpassword]=useState("");
  const[jzmm_state,setjzmm_state]=useState(localStorage.getItem("state"));
  const[register_username,setregister_username]=useState("");
  const[register_password1,setregister_password1]=useState("");
  const[register_password2,setregister_password2]=useState("");
  const[zcm,setzcm]=useState("");
  const ksdl_dom=useRef();
  const kszc_dom=useRef();
  const container_login=useRef();
  const container_register=useRef();

  useEffect(()=>{
    console.log(jzmm_state);
    if (jzmm_state==="1"){
      // 新代码
      setadmin(localStorage.getItem("admin"));
      setpassword(localStorage.getItem("password"));
      console.log("有缓存");
      setjzmm_state("1")
    }else{
        console.log("没有缓存");
        setjzmm_state("0");
    }
  },[])
  useEffect(()=>{
    console.log("变了"+jzmm_state);
  },[jzmm_state])

  const login=()=>{
      $.ajax({
        type: "get",
        url: "http://127.0.0.1:8081/admin/login?admin="+admin+"&password="+password,
        success: function (res){
            if (res.status===200&&res.data==="1") {
                console.log("登录成功现在state的状态"+jzmm_state);
                if (jzmm_state==="1") {
                    localStorage.setItem("state", "1");
                    localStorage.setItem("admin", admin);
                    localStorage.setItem("password", password);
                }else{
                    localStorage.setItem("state", "0");
                }
                // alert("登录成功");
                // 登录成功后修改路径
                localStorage.setItem("token", "eyJhbGciOiJIUzI1NiJ91");
                props.history.replace("/main");
            
            }else if(res.status===200&&res.data==="0"){
            alert("请检查用户名或密码是否正确");
            }
        },
        error:function(err){
            console.log("请求失败");
            console.log(err);
        }
      });
  }
  const register=()=>{
    if (zcm!=="root") {
      alert("请填写正确注册码！");
      return;
    }
    $.ajax({
      type: "get",
      url: "http://127.0.0.1:8081/admin/register?admin="+register_username+"&password="+register_password1+"&zcm="+zcm,
      success:function(res) {
          if (res.status===200&&res.data==="1"){
            alert("注册成功");
            // setadmin(register_username);
          }else if(res.status===200&&res.data==="0"){
            alert("注册失败");
          }else if(res.status===200&&res.data==="2"){
            alert("该账号已被注册");
          }
      },
      error:function(){
          alert("注册失败");
      }
    });
  }

  const qiehuan1=()=>{
    ksdl_dom.current.className="ksdl active";
    kszc_dom.current.className="kszc";
    container_login.current.style.display="block";
    container_register.current.style.display="none";
  }
  
  const qiehuan2=()=>{
    setregister_username("");
    setregister_password1("");
    setregister_password2("");
    setzcm("")
    ksdl_dom.current.className="ksdl";
    kszc_dom.current.className="kszc active";
    container_login.current.style.display="none";
    container_register.current.style.display="block";
  }
  
  return(
    <div className="App">
      <div className='title'>
        学生后台管理系统
        <span className='Vs'>
          V2023
        </span>
      </div>
      <div className='main'>
          <div className='main_header clearfix'>
              <span className="ksdl active" ref={ksdl_dom} onClick={qiehuan1}>快速登录</span>
              <span className="kszc" ref={kszc_dom} onClick={qiehuan2}>快速注册</span>
          </div>
          <div className="main_body ">
            <div className='main_body_login' ref={container_login}>
              <div className='content'>
                <div className="login_username"><label htmlFor="login_username">账号 : </label><input type="text" id='login_username' placeholder='请输入用户名' value={admin} onChange={(e)=>{setadmin(e.target.value)}}/></div>
                <div className="login_password"><label htmlFor="login_password">密码 : </label><input type="password"  id='login_password' placeholder='请输入密码' value={password} onChange={(e)=>{setpassword(e.target.value)}}/></div>
                <div className="jzmm"><input type="checkbox" id='jzmm' onChange={(e)=>{setjzmm_state((e.target.checked)?"1":"0")}} checked={jzmm_state==="1"?true:false}/><label htmlFor="jzmm">记住密码</label></div>
                <input type="button" id='login' value="登录" onClick={login}/>
              </div>
            </div>
            <div className='main_body_register' ref={container_register}>
              <div className="content">
                <div className="register_username"><label htmlFor="register_username">用户名 : </label><input type="text" id='register_username' placeholder='请输入用户名' onChange={(e)=>{setregister_username(e.target.value)}} value={register_username}/></div>
                <div className="register_password1"><label htmlFor="register_password1">密码 : </label><input type="password" id='register_password1' placeholder='请输入密码' onChange={(e)=>{setregister_password1(e.target.value)}} value={register_password1}/></div>
                <div className="register_password2"><label htmlFor="register_password2">确认密码 : </label><input type="password" id='register_password2' placeholder='请再次输入密码' onChange={(e)=>{setregister_password2(e.target.value)}} value={register_password2}/></div>
                <div className="email"><label htmlFor="zcm">注册码 : </label><input type="text" id='zcm' placeholder='请输入注册码' onChange={(e)=>{setzcm(e.target.value)}} value={zcm}/></div>
                <input type="button" id='register' value="注   册" onClick={register}/>
              </div>
            </div>
        </div>
      </div>
    </div>
  );
}
export default withRouter(Login_register);