import React, { useEffect, useState,useContext} from "react"
import $ from "jquery"
import {C} from "../.."
import "./index.css"
import { Pagination } from 'antd';
import "antd/dist/reset.css"
 const StudentList=(props)=>{

    // 接受上下文
    const {success_state,setsuccess_state} = useContext(C);
    var click_demo=null;
    const[students_arr,setstudents_arr]=useState([]);
    const[students_current,setstudents_current]=useState([]);
    const[current_index,setcurrent_index]=useState(1)
    const[max_index,setmax_index]=useState(1);
    // 修改框的input值
    const[bh,setbh]=useState("");
    const[xh,setxh]=useState("");
    const[xb,setxb]=useState("");
    const[xm,setxm]=useState("");
    const[yhm,setyhm]=useState("");
    const[mm,setmm]=useState("");
    
    const[search_value,setsearch_value]=useState("");

    useEffect(()=>{
        qingqiu();
    },[])

    function qingqiu() {
        $.ajax({
            type:"get",
            url:"http://127.0.0.1:8081/user/findAll",
            async:false,
            success:function(res){
                console.log("成功了！！");
                console.log(res);
                var resData=res.data;
                for(var i=0;i<resData.length;i++){
                    resData[i].id=i+1;
                }
                setstudents_arr(resData);
                setsearch_value("");
                console.log("当前索引"+current_index);
                var n=resData.length;  
                if (n%11!=0) {
                    console.log("没出开"+(parseInt((n/11))+1));
                    setmax_index(parseInt((n/11))+1)
                    if (parseInt((n/11))+1>current_index) {
                        setstudents_current(resData.slice((current_index-1)*11,current_index*11)); 
                        console.log("11");
                    }else if(parseInt((n/11))+1===current_index){
                        setstudents_current(resData.slice((current_index-1)*11,resData.length)); 
                        console.log("12");
                    }else if(parseInt((n/11))+1<current_index){
                        setstudents_current(resData.slice((current_index-2)*11,resData.length)); 
                        console.log("13");
                    }
                }else{
                    console.log("出开了"+parseInt((n/11)));
                    setmax_index(parseInt((n/11)))
                    if (parseInt((n/11))>current_index) {
                        setstudents_current(resData.slice((current_index-1)*11,current_index*11));
                        console.log("21"); 
                    }else if(parseInt((n/11))===current_index){
                        setstudents_current(resData.slice((current_index-1)*12,resData.length)); 
                        console.log("22");  
                    }else if(parseInt((n/11))<current_index){
                        setstudents_current(resData.slice((current_index-2)*11,resData.length)); 
                        console.log("23"); 
                        setcurrent_index((current_index)=>{
                            return current_index-1;
                        })
                    }

                }
            }
        })
    }
    // 删除
    const ondelete=(event)=>{
        // document.querySelector(".success_state").style.display="none";
        click_demo=event.target.parentNode.parentNode;
        setbh(click_demo.children[0].innerText);
        setxh(click_demo.children[1].innerText);
        setxm(click_demo.children[2].innerText);
        setxb(click_demo.children[3].innerText);
        setyhm(click_demo.children[4].innerText);
        setmm(click_demo.children[5].innerText);
        return 0; 
    }
    // 修改
    const onrevise=(event)=>{
        click_demo=event.target.parentNode.parentNode;
        setbh(click_demo.children[0].innerText);
        setxh(click_demo.children[1].innerText);
        setxm(click_demo.children[2].innerText);
        setxb(click_demo.children[3].innerText);
        setyhm(click_demo.children[4].innerText);
        setmm(click_demo.children[5].innerText);
        return 0;
    }
    // 确认删除
    const affirm=()=>{
        // document.querySelector(".success_state").style.display="none";
        $.ajax({
            type:"get",
            url:`http://127.0.0.1:8081/user/deleteUser?username=${yhm}`,
            async:false,
            success:function(res){
                if (res.status===200&&res.data==="1") {
                    // document.querySelector(".success_state").style.display="block";
                    setsuccess_state(true);
                    qingqiu()
                }
                console.log(res);   
            }
        })
        
    }
    // 确认修改
    const revise=()=>{
        console.log(`更改过后的学号=${xh} 姓名=${xm} 性别=${xb} 用户名=${yhm} 密码=${mm}`);
        $.ajax({
            type:"get",
            url:`http://127.0.0.1:8081/user/updateUser?username=${yhm}&nickname=${xm}&password=${mm}&student_id=${xh}&six=${xb}`,
            async:false,
            success:function(res){
                if (res.status===200&&res.data==="1") {
                    // 显示请求成功
                    // document.querySelector(".success_state").style.display="block";
                    setsuccess_state(true);
                    qingqiu()
                }
            }
        })
    }

    // 改变current
    const pn_change_current=(current)=>{
        setsearch_value("");
        setcurrent_index(current)
        if (current===max_index){
            setstudents_current(students_arr.slice((current-1)*11,students_arr.length));
        }else{
            setstudents_current(students_arr.slice((current-1)*11,current*11));
        }
        console.log(current);
        
    }
    const serach_students=(msg)=>{
        setsearch_value(msg);
        console.log(msg);
        if (msg==""){
            setcurrent_index(1)
            setstudents_current(students_arr.slice(0,11));
        }else{
            var arr=[];
            for(var i=0;i<students_arr.length;i++){
                if (students_arr[i].nickname===msg) {
                    arr.push(students_arr[i])
                }             
            }
            if(arr.length===0){
                setcurrent_index(1)
                setstudents_current([]);
            }else{
                setstudents_current(arr);
            }
            // setstudents_current([]);
            
        }
    }
    return (
        <div>
            <div className="addstudent_body_content">
                <div className="title_sutdent">学生列表</div>
                <div className="student_list_search">
                    <span><strong>输入想要搜索的学生姓名:</strong></span> 
                    <input type="text" value={search_value} placeholder="学生姓名" onChange={(e)=>{serach_students(e.target.value)}}/>
                </div>
                <div className="content_list">
                    <table>
                        <thead>
                            <tr>
                                <td>编号</td>
                                <td>学号</td>
                                <td>姓名</td>
                                <td>性别</td>
                                <td>用户名</td>
                                <td>密码</td>
                            </tr>
                        </thead>
                        <tbody>
                            {
                                students_current.map((item, index) => {
                                    return  <tr key={index}>
                                                <td><b>{item.id}</b></td>
                                                <td>{item.student_id}</td>
                                                <td>{item.nickname}</td>
                                                <td>{item.six}</td>
                                                <td>{item.username}</td>
                                                <td>{item.password}</td>
                                                <td className="studentslist_delete"><a href="#" onClick={(e)=>{ondelete(e)}} data-bs-toggle="modal" data-bs-target="#delModal">删除</a></td>
                                                <td className="studentslist_revise"><a href="#" onClick={(e)=>{onrevise(e)}} data-bs-toggle="modal" data-bs-target="#reeModal">修改</a></td>
                                            </tr>
                                })
                            }
                        </tbody>
                    </table>
                </div>
            </div>
            
            {/* <!-- 删除模态框 --> */}
            <div className="modal" id="delModal">
                <div className="modal-dialog">
                    <div className="modal-content">

                    {/* <!-- 模态框头部 --> */}
                    <div className="modal-header">
                        <h4 className="modal-title">是否要删除该学生？</h4>
                        <button type="button" className="btn-close" data-bs-dismiss="modal"></button>
                    </div>

                    {/* <!-- 模态框内容 --> */}
                    <div className="modal-body">
                        <ul>
                            <li>编号:<b>{bh}</b></li>
                            <li>学号:<b>{xh}</b></li>
                            <li>姓名:<b>{xm}</b></li>
                            <li>性别:<b>{xb}</b></li>
                            <li>用户名:<b>{yhm}</b></li>
                            <li>密码:<b>{mm}</b></li>
                        
                        </ul>
                    </div>

                    {/* <!-- 模态框底部 --> */}
                    <div className="modal-footer">
                        <button type="button" className="btn btn-primary" data-bs-dismiss="modal" >取消</button>
                        <button type="button" className="btn btn-danger" data-bs-dismiss="modal" onClick={affirm}>删除</button>                            
                    </div>

                    </div>
                </div>
            </div>

            {/* <!-- 修改模态框 --> */}
            <div className="modal" id="reeModal">
                <div className="modal-dialog">
                    <div className="modal-content">

                    {/* <!-- 模态框头部 --> */}
                    <div className="modal-header">
                        <h4 className="modal-title">是否要修改该学生？</h4>
                        <button type="button" className="btn-close" data-bs-dismiss="modal"></button>
                    </div>

                    {/* <!-- 模态框内容 --> */}
                    <div className="modal-body">
                        <ul className="xiugaiul">
                            <li>编号:<b>{bh}</b></li>
                            <li>学号:<input type="text" value={xh||''} onChange={(e)=>{setxh(e.target.value)}} style={{border:"none",borderBottom:"1px solid rgb(0,0,0)",outline:"none",marginLeft:"5px",paddingLeft:"2px"}}/></li>
                            <li>姓名:<input type="text" value={xm||''} onChange={(e)=>{setxm(e.target.value)}} style={{border:"none",borderBottom:"1px solid rgb(0,0,0)",outline:"none",marginLeft:"5px",paddingLeft:"2px"}}/></li>
                            <li>性别:  <input type="radio" name="sex" value="男" id="man" onChange={(e)=>{setxb(e.target.value)}}  checked={xb==="男"}/><label htmlFor="man">男</label>   <input type="radio" name="sex" value="女" id="wuman" onChange={(e)=>{setxb(e.target.value)}} checked={xb==="女"}/><label htmlFor="wuman">女</label></li>
                            <li>用户名:<b>{yhm||''}</b></li>
                            <li>密码:<input type="text" value={mm||''} onChange={(e)=>{setmm(e.target.value)}} style={{border:"none",borderBottom:"1px solid rgb(0,0,0)",outline:"none",marginLeft:"5px",paddingLeft:"2px"}}/></li>
                        </ul>
                    </div>

                    {/* <!-- 模态框底部 --> */}
                    <div className="modal-footer">
                        <button type="button" className="btn btn-primary" data-bs-dismiss="modal" >取消</button>
                        <button type="button" className="btn btn-warning" data-bs-dismiss="modal" onClick={revise}>确认更改</button>                            
                    </div>

                    </div>
                </div>
            </div>

            {/* 分页 */}
            <Pagination className="pn" current={current_index}  onChange={pn_change_current} defaultCurrent={1} total={max_index*10} />

            

            
        </div>
    )
}
export default StudentList;