import React, { useState } from "react"
import "./index.css"
import $ from "jquery"
export default function UserRegister (props){
    const[username,setusername]=useState("");
    const[nickname,setnickname]=useState("");
    const[password,setpassword]=useState("");
    const[student_id,setstudent_id]=useState("")
    const[xb,setxb]=useState("男")
    const register=()=>{
        $.ajax({
            type:"post",
            url:"http://127.0.0.1:8081/user/register",
            data:{"username":username,"nickname":nickname,"password":password,"student_id":student_id,"six":xb},
            success:function(res){
                console.log(res);
                if(res.status=="200"&&res.data=="1"){
                    alert("注册成功！ 即将跳转登录页面~");
                    sessionStorage.setItem("username",username);
                    sessionStorage.setItem("password",password);
                    setInterval(function(){
                        props.history.push("/userlogin")
                    },1000);
                }else if(res.status=="200"&&res.data=="2"){
                    alert("此帐号已被注册了！");
                }else if(res.status=="200"&&res.data=="0"){
                    alert("网络拥挤 请重试！");
                }
            },
            error:function(err){
                console.log("注册失败 服务器端错误 500");
                console.log(err);
            }
            
        });
    }
    return(
        <div>
            <div className="register_box">
                <div className="title">
                    <h2>欢迎注册</h2>
                </div>
                <div className="xxx">
                    <span id="unStatus">
                            ✔
                        </span>
                    <span id="pwStatus">
                            ✔
                        </span>
                    <div className="un">
                        <input type="text" placeholder="请输入您的用户名" id="username" value={username} onChange={(e)=>{setusername(e.target.value)}}/>
                    </div>
                    <div className="un">
                        <input type="text" placeholder="请输入您的姓名" id="name" value={nickname} onChange={(e)=>{setnickname(e.target.value)}}/>
                    </div>
                    <div className="pw">
                        <input type="password" placeholder="请输入您的密码" id="password" value={password} onChange={(e)=>{setpassword(e.target.value)}}/>
                    </div>
                    <div className="xh">
                        <input type="text" placeholder="请输入您的学号" id="xh" value={student_id} onChange={(e)=>{setstudent_id(e.target.value)}}/>
                    </div>
                    <div className="xb">
                        <input type="radio" id="main" value="男" name="sex" onChange={(e)=>{setxb(e.target.value)}} defaultChecked={xb==="男"?true:false}/><label htmlFor="main">男</label>
                        <input type="radio" id="wuman" value="女" name="sex" onChange={(e)=>{setxb(e.target.value)}} defaultChecked={xb==="女"?true:false}/><label htmlFor="wuman">女</label>
                    </div>
                    <div className="b">
                        <input type="button" value="注册" id="btn" onClick={register}/>
                    </div>
                </div>
            </div>
        </div>
    )
}