import React, { useEffect, useState } from "react"
import "./index.css"
import $ from "jquery"
export default function UsserLogin(props){

	const[un,setun]=useState(sessionStorage.getItem("username"));
	const[pwd,setpwd]=useState(sessionStorage.getItem("password"));

    const goRegister=()=>{
        props.history.push("/userregister")
    }
	
	useEffect(()=>{
		if (un!==""&&pwd!=="") {
			sessionStorage.setItem("username","");
			sessionStorage.setItem("password","");
		}
	},[])

	const login=()=>{
		$.ajax({
			type:"post",
			url:"http://127.0.0.1:8081/user/login",
			data:{"username":un,"password":pwd},
			success:function(res){
				console.log(res);
				if(res.status=="200"&&res.data=="1"){
					console.log("登录成功~");
					alert("登录成功！");
				}else if(res.status=="200"&&res.data=="0"){
					console.log("登录失败！");
					alert("请检查 用户名 或 密码 是否有误！");
				}
			},
			error:function(err){
				console.log("服务器错误！");
				console.log(err);
			}
		});
	}
    return(
        <div>
            <div className="box">
			<div className="title">
				<h2>会员登录</h2>
			</div>
			<div className="xxx">
					<div className="un">
						<input type="text" placeholder="请输入您的用户名" id="username" value={un} onChange={(e)=>{setun(e.target.value)}} />
					</div>
					<div className="pw">
						<input type="password" placeholder="请输入您的密码" id="password" value={pwd} onChange={(e)=>{setpwd(e.target.value)}}/>
					</div>
					<div className="b">
						<input type="button" value="立即登录" id="btn" onClick={login}/>
					</div>
			</div>
			<div className="zaxiang">
				<span>没有账号?</span>
				<a href="#" onClick={goRegister}>立即注册</a>
				<a href="">忘记密码</a>
			</div>
		    </div>
        </div>
    )
}