import React, { useContext,useEffect,useState } from "react"
import {C} from "../../../main"
import $ from "jquery"
export default function ModfilyStudent(){
    const {success_state,setsuccess_state} = useContext(C);

    const[username,setusername]=useState("");
    const[nickname,setnickname]=useState("");
    const[password,setpassword]=useState("");
    const[student_id,setstudent_id]=useState("");
    const[studentsix,setstudentsix]=useState("男");
    const[timeout,settimeout]=useState("");
    const[select_state,setselect_state]=useState(false);
    useEffect(()=>{
        setsuccess_state(false);
    },[])
    const username_change_fun=(val)=>{
        setusername(val);
        console.log(val);
        clearTimeout(timeout);
        settimeout(setTimeout(function(){
        	$.ajax({
                type:"get",
                url:`http://127.0.0.1:8081/user/findByUsername?username=${val}`,
                async:false,
                success:function(res){
                    if (res.status===200) {
                        console.log("查询到了");
                        console.log(res.data);
                        setusername(res.data.username);
                        setnickname(res.data.nickname);
                        setpassword(res.data.password);
                        setstudent_id(res.data.student_id);
                        setstudentsix(res.data.six);
                        setsuccess_state(false);
                        // 设置是否查询到结果
                        setselect_state(true);
                    }else{
                        console.log("未找到该用户");
                        setnickname("");
                        setpassword("");
                        setstudent_id("");
                        setstudentsix("男");
                        setsuccess_state(false);
                        setselect_state(false);
                    }
                }
            })
    	}, 1000))

    }
    const Confirm_modification=()=>{
        if (select_state) {
            $.ajax({
                type:"get",
                url:`http://127.0.0.1:8081/user/updateUser?username=${username}&nickname=${nickname}&password=${password}&student_id=${student_id}&six=${studentsix}`,
                async:false,
                success:function(res){
                    console.log(res);
                    if (res.status===200&&res.data==="1") {
                        console.log(res.data);
                        setusername("");
                        setnickname("");
                        setpassword("");
                        setstudent_id("");
                        setstudentsix("男");
                        setsuccess_state(true);
                        // 设置是否查询到结果
                        setselect_state(false);
                    }else{
                        setnickname("");
                        setpassword("");
                        setstudent_id("");
                        setstudentsix("男");
                        setsuccess_state(false);
                        setselect_state(false);
                    }
                }
            })
        }else{
            alert("查询不到该学生");
        }
    }
    return(
        <div className="addstudent_body_content">
            <div className="title_sutdent">修改学生</div>
            <div className="addstudent_context">
                <span style={{color:"red",fontSize:"20px"}}>*注意:学生账号是不可修改的！！！</span>
                <div className="addstudent_context_item">
                    <div className="addstudent_context_item_title">
                        <label htmlFor="xszh"><b>学生账号:</b></label>
                    </div>
                    <input type="text" value={username} onChange={(e)=>{username_change_fun(e.target.value)}} id="xszh" className="form-control" placeholder="请输入学生账号"/>
                    {username===""?(<span>学生账号不能为空</span>):""}
                </div>
                <div className="addstudent_context_item">
                    <div className="addstudent_context_item_title">
                        <label htmlFor="xsxm"><b>学生姓名:</b></label>
                    </div>
                    <input type="text" value={nickname} onChange={(e)=>{setnickname(e.target.value)}} id="xsxm" className="form-control" placeholder="请输入学生姓名"/>
                    {nickname===""?(<span>学生姓名不能为空</span>):""}
                </div>
                <div className="addstudent_context_item">
                    <div className="addstudent_context_item_title">
                        <label htmlFor="xsmm"><b>学生密码:</b></label>
                    </div>
                    <input type="text" value={password} onChange={(e)=>{setpassword(e.target.value)}} id="xsmm" className="form-control" placeholder="请输入学生密码"/>
                    {password===""?(<span>学生密码不能为空</span>):""}
                </div>
                <div className="addstudent_context_item">
                    <div className="addstudent_context_item_title">
                        <label htmlFor="xsxh"><b>学生学号:</b></label>
                    </div>
                    <input type="text" value={student_id} onChange={(e)=>{setstudent_id(e.target.value)}} id="xsxh" className="form-control" placeholder="请输入学生学号"/>
                    {student_id===""?(<span>学生学号不能为空</span>):""}
                </div>
                <div className="addstudent_context_item">
                    <div className="addstudent_context_item_title">
                        <label htmlFor="xsbh"><b>学生性别:</b></label>
                    </div>
                    <input type="radio" id="man" value="男" name="six" style={{width:"15px"}} onChange={(e)=>{setstudentsix(e.target.value)}} checked={studentsix==="男"?true:false}/>
                    <label htmlFor="man" style={{float:"left",marginLeft:"10px",lineHeight:"30px"}}>男</label>
                    <input type="radio" id="wuman" value="女" name="six" style={{width:"15px",marginLeft:"10px"}} onChange={(e)=>{setstudentsix(e.target.value)}} checked={studentsix==="女"?true:false}/>
                    <label htmlFor="wuman" style={{float:"left",marginLeft:"10px",lineHeight:"30px"}}>女</label>
                    {/* {studentsix===""?(<span style={{marginLeft:"138px"}}>请选择性别</span>):""} */}
                </div>
                <button type="button" 
                        className="btn btn-warning" 
                        style={{marginLeft:"130px",marginTop:"20px"}} 
                        data-bs-toggle="modal" 
                        data-bs-target="#revise">
                        修改
                </button>

                {/* <!-- 删除模态框 --> */}
                <div className="modal" id="revise">
                    <div className="modal-dialog">
                        <div className="modal-content">

                        {/* <!-- 模态框头部 --> */}
                        <div className="modal-header">
                            <h4 className="modal-title">是否要修改该学生？</h4>
                            <button type="button" className="btn-close" data-bs-dismiss="modal"></button>
                        </div>

                        {/* <!-- 模态框内容 --> */}
                        <div className="modal-body">
                            <ul>
                                <li>学号:<b>{student_id}</b></li>
                                <li>姓名:<b>{nickname}</b></li>
                                <li>性别:<b>{studentsix}</b></li>
                                <li>用户名:<b>{username}</b></li>
                                <li>密码:<b>{password}</b></li>
                            
                            </ul>
                        </div>

                        {/* <!-- 模态框底部 --> */}
                        <div className="modal-footer">
                            <button type="button" className="btn btn-primary" data-bs-dismiss="modal" >取消</button>
                            <button type="button" className="btn btn-danger" data-bs-dismiss="modal" onClick={Confirm_modification}>确认更改</button>                            
                        </div>

                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}